<template>
<div :style='{"width":"100%","padding":"30px calc((100% - 1200px)/2) 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <el-form
	  :style='{"border":"1px solid #eee","padding":"30px 0","alignItems":"flex-start","flexWrap":"wrap","background":"#fff","display":"flex","width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="饮食名称" prop="yinshimingcheng">
            <el-input v-model="ruleForm.yinshimingcheng" 
                placeholder="饮食名称" clearable :disabled=" false  ||ro.yinshimingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'  label="饮食类型" prop="yinshileixing">
            <el-select v-model="ruleForm.yinshileixing" placeholder="请选择饮食类型" :disabled=" false  ||ro.yinshileixing" >
              <el-option
                  v-for="(item,index) in yinshileixingOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'  label="制作难度" prop="zhizuonandu">
            <el-select v-model="ruleForm.zhizuonandu" placeholder="请选择制作难度" :disabled=" false  ||ro.zhizuonandu" >
              <el-option
                  v-for="(item,index) in zhizuonanduOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="主要材料" prop="zhuyaocailiao">
            <el-input v-model="ruleForm.zhuyaocailiao" 
                placeholder="主要材料" clearable :disabled=" false  ||ro.zhuyaocailiao"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="营养价值" prop="yingyangjiazhi">
            <el-input v-model="ruleForm.yingyangjiazhi" 
                placeholder="营养价值" clearable :disabled=" false  ||ro.yingyangjiazhi"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="适宜人群" prop="shiyirenqun">
            <el-input v-model="ruleForm.shiyirenqun" 
                placeholder="适宜人群" clearable :disabled=" false  ||ro.shiyirenqun"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="饮食视频" v-if="type!='cross' || (type=='cross' && !ro.yinshishipin)" prop="yinshishipin">
            <file-upload
            tip="点击上传饮食视频"
            action="file/upload"
            :limit="1"
			:type="2"
            :multiple="true"
            :fileUrls="ruleForm.yinshishipin?ruleForm.yinshishipin:''"
            @change="yinshishipinUploadChange"
            ></file-upload>
          </el-form-item>
		  <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' class="upload" v-else label="饮食视频" prop="yinshishipin">
			<el-button v-if="ruleForm.yinshishipin" @click="download(baseUrl + ruleForm.yinshishipin)" type="success">预览</el-button>
			<el-button v-else disabled>暂无</el-button>
		  </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="饮食图片" v-if="type!='cross' || (type=='cross' && !ro.yinshitupian)" prop="yinshitupian">
            <file-upload
            tip="点击上传饮食图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.yinshitupian?ruleForm.yinshitupian:''"
            @change="yinshitupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' class="upload" v-else label="饮食图片" prop="yinshitupian">
                <img v-if="ruleForm.yinshitupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.yinshitupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.yinshitupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="制作步骤" prop="zhizuobuzhou">
            <editor 
                :style='{"minHeight":"250px","padding":"0","margin":"0","borderColor":"#1abc9e30","backgroundColor":"none","borderRadius":"0","borderWidth":"0px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.zhizuobuzhou" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"width":"100%","padding":"0","margin":"0","textAlign":"center"}'>
        <el-button :style='{"border":"0px solid #eccc19","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#fff","borderRadius":"20px","background":"linear-gradient(to right,#f67536,#f64d36),#f64d36","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"1px solid #ccc","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#666","borderRadius":"20px","background":"none","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				yinshimingcheng : false,
				yinshileixing : false,
				zhizuonandu : false,
				zhuyaocailiao : false,
				yingyangjiazhi : false,
				shiyirenqun : false,
				yinshishipin : false,
				zhizuobuzhou : false,
				yinshitupian : false,
				clicktime : false,
				clicknum : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          yinshimingcheng: '',
          yinshileixing: '',
          zhizuonandu: '',
          zhuyaocailiao: '',
          yingyangjiazhi: '',
          shiyirenqun: '',
          yinshishipin: '',
          zhizuobuzhou: '',
          yinshitupian: '',
          clicktime: '',
          clicknum: '',
        },
        yinshileixingOptions: [],
        zhizuonanduOptions: [],


        rules: {
          yinshimingcheng: [
          ],
          yinshileixing: [
          ],
          zhizuonandu: [
          ],
          zhuyaocailiao: [
          ],
          yingyangjiazhi: [
          ],
          shiyirenqun: [
          ],
          yinshishipin: [
          ],
          zhizuobuzhou: [
          ],
          yinshitupian: [
          ],
          clicktime: [
          ],
          clicknum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='yinshimingcheng'){
              this.ruleForm.yinshimingcheng = obj[o];
              this.ro.yinshimingcheng = true;
              continue;
            }
            if(o=='yinshileixing'){
              this.ruleForm.yinshileixing = obj[o];
              this.ro.yinshileixing = true;
              continue;
            }
            if(o=='zhizuonandu'){
              this.ruleForm.zhizuonandu = obj[o];
              this.ro.zhizuonandu = true;
              continue;
            }
            if(o=='zhuyaocailiao'){
              this.ruleForm.zhuyaocailiao = obj[o];
              this.ro.zhuyaocailiao = true;
              continue;
            }
            if(o=='yingyangjiazhi'){
              this.ruleForm.yingyangjiazhi = obj[o];
              this.ro.yingyangjiazhi = true;
              continue;
            }
            if(o=='shiyirenqun'){
              this.ruleForm.shiyirenqun = obj[o];
              this.ro.shiyirenqun = true;
              continue;
            }
            if(o=='yinshishipin'){
              this.ruleForm.yinshishipin = obj[o];
              this.ro.yinshishipin = true;
              continue;
            }
            if(o=='zhizuobuzhou'){
              this.ruleForm.zhizuobuzhou = obj[o];
              this.ro.zhizuobuzhou = true;
              continue;
            }
            if(o=='yinshitupian'){
              this.ruleForm.yinshitupian = obj[o].split(",")[0];
              this.ro.yinshitupian = true;
              continue;
            }
            if(o=='clicktime'){
              this.ruleForm.clicktime = obj[o];
              this.ro.clicktime = true;
              continue;
            }
            if(o=='clicknum'){
              this.ruleForm.clicknum = obj[o];
              this.ro.clicknum = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.$http.get('option/yinshileixing/yinshileixing', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.yinshileixingOptions = res.data.data;
          }
        });
        this.zhizuonanduOptions = "★★★★★,★★★★,★★★,★★,★".split(',')

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`yinshixinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {
			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('yinshixinxi/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`yinshixinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`yinshixinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      yinshishipinUploadChange(fileUrls) {
          this.ruleForm.yinshishipin = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
      yinshitupianUploadChange(fileUrls) {
          this.ruleForm.yinshitupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
