<template>
<div :style='{"width":"100%","padding":"30px calc((100% - 1200px)/2) 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <el-form
	  :style='{"border":"1px solid #eee","padding":"30px 0","alignItems":"flex-start","flexWrap":"wrap","background":"#fff","display":"flex","width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="食物名称" prop="shiwumingcheng">
            <el-input v-model="ruleForm.shiwumingcheng" 
                placeholder="食物名称" clearable :disabled=" false  ||ro.shiwumingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="蛋白质" prop="danbaizhi">
			<el-input-number v-model="ruleForm.danbaizhi" placeholder="蛋白质" :disabled="ro.danbaizhi"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="脂肪" prop="zhifang">
			<el-input-number v-model="ruleForm.zhifang" placeholder="脂肪" :disabled="ro.zhifang"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="碳水化合物" prop="tanshuihuahewu">
			<el-input-number v-model="ruleForm.tanshuihuahewu" placeholder="碳水化合物" :disabled="ro.tanshuihuahewu"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="膳食纤维" prop="shanshixianwei">
			<el-input-number v-model="ruleForm.shanshixianwei" placeholder="膳食纤维" :disabled="ro.shanshixianwei"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="热量" prop="reliang">
			<el-input-number v-model="ruleForm.reliang" placeholder="热量" :disabled="ro.reliang"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="维生素" prop="weishengsu">
			<el-input-number v-model="ruleForm.weishengsu" placeholder="维生素" :disabled="ro.weishengsu"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="食物图片" v-if="type!='cross' || (type=='cross' && !ro.shiwutupian)" prop="shiwutupian">
            <file-upload
            tip="点击上传食物图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.shiwutupian?ruleForm.shiwutupian:''"
            @change="shiwutupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' class="upload" v-else label="食物图片" prop="shiwutupian">
                <img v-if="ruleForm.shiwutupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.shiwutupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.shiwutupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="相关信息" prop="xiangguanxinxi">
            <editor 
                :style='{"minHeight":"250px","padding":"0","margin":"0","borderColor":"#1abc9e30","backgroundColor":"none","borderRadius":"0","borderWidth":"0px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.xiangguanxinxi" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"width":"100%","padding":"0","margin":"0","textAlign":"center"}'>
        <el-button :style='{"border":"0px solid #eccc19","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#fff","borderRadius":"20px","background":"linear-gradient(to right,#f67536,#f64d36),#f64d36","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"1px solid #ccc","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#666","borderRadius":"20px","background":"none","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				shiwumingcheng : false,
				danbaizhi : false,
				zhifang : false,
				tanshuihuahewu : false,
				shanshixianwei : false,
				reliang : false,
				weishengsu : false,
				xiangguanxinxi : false,
				shiwutupian : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          shiwumingcheng: '',
          danbaizhi: '',
          zhifang: '',
          tanshuihuahewu: '',
          shanshixianwei: '',
          reliang: '',
          weishengsu: '',
          xiangguanxinxi: '',
          shiwutupian: '',
        },


        rules: {
          shiwumingcheng: [
          ],
          danbaizhi: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          zhifang: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          tanshuihuahewu: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          shanshixianwei: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          reliang: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          weishengsu: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          xiangguanxinxi: [
          ],
          shiwutupian: [
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='shiwumingcheng'){
              this.ruleForm.shiwumingcheng = obj[o];
              this.ro.shiwumingcheng = true;
              continue;
            }
            if(o=='danbaizhi'){
              this.ruleForm.danbaizhi = obj[o];
              this.ro.danbaizhi = true;
              continue;
            }
            if(o=='zhifang'){
              this.ruleForm.zhifang = obj[o];
              this.ro.zhifang = true;
              continue;
            }
            if(o=='tanshuihuahewu'){
              this.ruleForm.tanshuihuahewu = obj[o];
              this.ro.tanshuihuahewu = true;
              continue;
            }
            if(o=='shanshixianwei'){
              this.ruleForm.shanshixianwei = obj[o];
              this.ro.shanshixianwei = true;
              continue;
            }
            if(o=='reliang'){
              this.ruleForm.reliang = obj[o];
              this.ro.reliang = true;
              continue;
            }
            if(o=='weishengsu'){
              this.ruleForm.weishengsu = obj[o];
              this.ro.weishengsu = true;
              continue;
            }
            if(o=='xiangguanxinxi'){
              this.ruleForm.xiangguanxinxi = obj[o];
              this.ro.xiangguanxinxi = true;
              continue;
            }
            if(o=='shiwutupian'){
              this.ruleForm.shiwutupian = obj[o].split(",")[0];
              this.ro.shiwutupian = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`shiwuyingyang/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {
			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('shiwuyingyang/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`shiwuyingyang/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`shiwuyingyang/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      shiwutupianUploadChange(fileUrls) {
          this.ruleForm.shiwutupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

